			<div class="about-container">
	    		<p>لأجل تحقيق الغرض منها فإن هذه الخدمة توظّف تقنيات معلوماتية متنوّعة لمعالجة و&nbsp;حفظ البيانات التي يدخلها مستخدموها، بما في ذلك بيانات يُدخلها المستخدمون بأنفسهم و&nbsp;بيانات تتولّد نتيجة اشتغال النظّم العديدة التي تتكوّن منها الخدمة.</p>
				<p>فعند إيداع المستخدم استمارة طلب إدراج مُدخلة تَحفظُ الخدمة كلَّ البيانات المُدخلة في الاستمارة، كما يُحفظُ تاريخ إيداع طلب الإدراج. و&nbsp;فيما عدا البيانات المدخلة في الاستمارة لا تحفظ الخدمة أية بيانات أخرى مستنبطة من اتّصال المستخدم بالخدمة عبر الإنترنت، لا عنوان الإنترنت المتّصل منه، و&nbsp;لا معلومات جغرافية، و&nbsp;لا مُعرّفات مميزة لعتاد أو برمجيات المستخدم، و&nbsp;لا بيانات يمكن منها تمييز المستخدم كفرد فيما عدا ما يدخله بنفسه في الاستمارة.</p>
				<p>تُحذف المدخلات من سجلّ المدخلات الجارية لأحد الأسباب التالية:</p>
				<ul>
