					<li>انقضاء مدة صلاحية الدواء المُدخلة</li>
					<li>بطلب المستخدم الذي أدخلها</li>
				</ul>
				<div style="height: 12px;"></div>
				<p>بعد حذف مُدخلة من سجّل المدخلات الجارية تُمحى بعض بياناتها و&nbsp;يُنقل بعضها الآخر في صيغته الأصلية أو صيغة مشتقة منها إلى الأرشيف. البيانات التي تُحفظ في الأرشيف هي:</p>
				<ul>
					<li>اسم الدواء</li>
					<li>المحافظة</li>
					<li>تلبيدة عنوان البريد الإلكتروني</li>
					<li>تلبيدة رقم التلفون إذا أدخِل</li>
					<li>سبب و&nbsp;تاريخ الحذف من سجّل المدخلات الجارية.</li>
				</ul>
				<p>التلبيدة قطعة معلوماتية مستنبطة مِنْ دخل ما بحيث تكون فريدة لكل دخل مختلف و&nbsp;متطابقة لكل الدخل المتطابق، و&nbsp;هي خرج تطبيق دالة غير عكوسة على الدَّخل. أي أنّه لا يمكن استنباط الدخل من الخرج.</p>
				<p>لا يلزم لاستخدام الخدمة فتح حسابات و&nbsp;لا الولوج إليها لذا لا تحفظ أسماء مستخدمين و&nbsp;لا كلمات سرّ أو ماشابهها.</p>
				<p>عناوين البريد المدخلة أثناء الاستخدام لا تُحفظ إلا للمدة اللازمة للإجراء المطلوب و&nbsp;لا تُتاح لأية أطراف خارجية.</p>
